import React from 'react';
import {
  VStack,
} from '@chakra-ui/react';
import { useWindowWidth } from '@react-hook/window-size';

import underConstructionImgMb from "./assets/img/underconstruction.svg";
import underConstructionImgPc from "./assets/img/underconstructionpc.svg";

function App() {
  const windowWidth = useWindowWidth();
  return (
    <VStack bgColor="black" w="full" h="100vh" bgImage={windowWidth < 450 ? underConstructionImgMb : underConstructionImgPc} bgRepeat="no-repeat" bgPosition="center" bgSize="cover"></VStack>
    
  );
}

export default App;
